.arrow_box {
    position: relative;
    background: #f5f5f5;
    /* border: 1px solid #ccc; */
    box-shadow: 0px 0px 5px #ccc; 
    max-width: 250px; 
    width: 100%; padding: 15px;
}
.arrow_box:after, .arrow_box:before {
    right: 100%;
    top: 25px;
    border: solid transparent;
    content: " ";
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
}

.arrow_box:after {
    border-color: rgba(245, 245, 245, 0);
    border-right-color: #f5f5f5;
    border-width: 10px;
    margin-top: -10px;
}
.arrow_box:before {
