.stepBar ul li::before{width: 50%; margin-top: -12px; background: #f1f7f7; position: absolute; left: 0px; top: 50%; z-index: 1;  content: ""; height: 2px;}
.stepBar ul li::after{width: 50%; margin-top: -12px; background: #f1f7f7; position: absolute; left: 50%; top: 50%; z-index: 1;  content: ""; height: 2px;}
.stepBar ul li:first-child::before{ left: 50%}
